import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Typography from '@material-ui/core/Typography';
import Alert from '@material-ui/lab/Alert';
import Container from '@material-ui/core/Container';
import Button from '@material-ui/core/Button';
import {Link as ReactLink, Redirect} from 'react-router-dom';
import userStore from '../stores/UserStore';
import qs from 'query-string';
import axios from 'axios';


const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1
    },
    menuButton: {
        marginRight: theme.spacing(2)
    },
    title: {
        flexGrow: 1
    },
    slack: {
        width: 32,
        height: 32,
        marginRight: theme.spacing(2)
    },
    container: {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(5),
        paddingTop: theme.spacing(3),
        paddingBottom: theme.spacing(3),
        paddingLeft: theme.spacing(10),
        paddingRight: theme.spacing(10),
        '& .MuiButton-root': {
            marginTop: theme.spacing(2)
        }
    }
}));

export default function OAuth() {
    const classes = useStyles();
    const [authenticated, setAuthenticated] = useState(false);
    const [severity, setSeverity] = useState('success');
    const [message, setMessage] = useState('ログイン中です...');

    useEffect(() => {
        userStore.on('change', () => {
            setAuthenticated(userStore.isAuthenticated());
        });
        const code = qs.parse(window.location.search).code;
        if (code !== undefined) {
            axios.get('https://slack.com/api/oauth.access', {
                params: {
                    client_id: process.env.REACT_APP_OAUTH_CLIENT_ID,
                    client_secret: process.env.REACT_APP_OAUTH_CLIENT_SECRET,
                    code: code,
                    redirect_uri: process.env.REACT_APP_OAUTH_REDIRECT
                }
            }).then((response) => {
                if (response.data['ok']) {
                    const accessToken = response.data['access_token'];
                    const userId = response.data['user_id'];
                    axios.get('https://slack.com/api/users.info', {
                        params: {
                            token: accessToken,
                            user: userId
                        }
                    }).then((response) => {
                        if (response.data['ok']) {
                            userStore.setUser(response.data['user']);
                            userStore.setAccessToken(accessToken);
                            userStore.setAuthenticated(true);
                            userStore.removeAllListeners('change');
                        }
                        else {
                            setSeverity('error');
                            setMessage(response.data['error']);
                        }
                    }).catch((error) => {
                        setSeverity('error');
                        setMessage(error['message']);
                    });
                }
                else {
                    setSeverity('error');
                    setMessage(response.data['error']);
                }
            }).catch((error) => {
                setSeverity('error');
                setMessage(error['message']);
            });
        }
        else {
            setSeverity('error');
            setMessage('codeの指定がないため認証できません。');
        }
    },[]);

    return authenticated ? (<Redirect to='/timerecord'/>) : (
        <div className={classes.root}>
            <AppBar position="sticky">
                <Toolbar>
                    <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" className={classes.title}>
                        株式会社ベイクラフト
                    </Typography>
                </Toolbar>
            </AppBar>

            <Container maxWidth="md" className={classes.container}>
                {'success' === severity ? (<div>{message}</div>) : (
                    <div>
                        <Alert severity={severity}>{message}</Alert>
                        <div><Button component={ReactLink} to="/" color="primary">TOP画面に戻る</Button></div>
                    </div>
                )}
            </Container>
        </div>
    );
}
