import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import MenuIcon from "@material-ui/icons/Menu";
import IconButton from "@material-ui/core/IconButton";
import Drawer from "@material-ui/core/Drawer";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import {Link} from "react-router-dom";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import AccessTimeIcon from "@material-ui/icons/AccessTime";
import ListItemText from "@material-ui/core/ListItemText";
import Divider from "@material-ui/core/Divider";
import TimelapseIcon from "@material-ui/icons/Timelapse";
import TimerOffIcon from "@material-ui/icons/TimerOff";


const useStyles = makeStyles(theme => ({
    menuButton: {
        marginRight: theme.spacing(2)
    },
    drawerList: {
        width: 250
    }
}));

export default function SideMenu() {
    const classes = useStyles();
    const [drawerOpen, setDrawerOpen] = useState(false);

    return (
        <div>
            <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu" onClick={() => setDrawerOpen(true)}>
                <MenuIcon />
            </IconButton>
            <Drawer open={drawerOpen} onClose={() => setDrawerOpen(false)}>
                <div className={classes.drawerList} onClick={() => setDrawerOpen(false)} onKeyDown={() => setDrawerOpen(false)}>
                    <List component="nav">
                        <ListItem button component={Link} to="/timerecord">
                            <ListItemIcon><AccessTimeIcon/></ListItemIcon>
                            <ListItemText primary="勤怠記録"/>
                        </ListItem>
                        <Divider />
                        <ListItem button component={Link} to="/fixedtime">
                            <ListItemIcon><TimelapseIcon/></ListItemIcon>
                            <ListItemText primary="所定時間設定"/>
                        </ListItem>
                        <ListItem button component={Link} to="/breaktime">
                            <ListItemIcon><TimerOffIcon/></ListItemIcon>
                            <ListItemText primary="休憩時間設定"/>
                        </ListItem>
                    </List>
                </div>
            </Drawer>
        </div>
    );
}
