import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Avatar from '@material-ui/core/Avatar';
import Popper from '@material-ui/core/Popper';
import Paper from '@material-ui/core/Paper';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import MenuList from '@material-ui/core/MenuList';
import MenuItem from '@material-ui/core/MenuItem';
import userStore from '../stores/UserStore';
import useReactRouter from 'use-react-router';


const useStyles = makeStyles(theme => ({
    userAvatar: {
        marginRight: theme.spacing(2)
    }
}));

export default function UserAvatar() {
    const classes = useStyles();
    const [user] = useState(userStore.getUser());
    const [anchorEl, setAnchorEl] = useState(null);
    const { history } = useReactRouter();

    /**
     * ユーザボタンがクリックされた場合に呼ばれます。
     *
     * @param event イベント
     */
    const handleClickUser = (event) => {
        setAnchorEl(anchorEl ? null : event.currentTarget);
    };

    /**
     * ユーザメニューでキーダウンイベントが発生した場合に呼ばれます。
     *
     * @param event イベント
     */
    const handleKeyDownUserMenu = (event) => {
        if ('Tab' === event.key) {
            event.preventDefault();
            setAnchorEl(null);
        }
    };

    /**
     * ユーザメニュー以外がクリックされた場合に呼ばれます。
     */
    const handleClickAway = () => {
        setAnchorEl(null);
    };

    /**
     * ログアウトメニューが選択された場合に呼ばれます。
     */
    const handleSelectLogout = () => {
        userStore.setUser(null);
        userStore.setAccessToken(null);
        userStore.setAuthenticated(false);
        history.push('/');
    };

    return (
        <div>
            <Button color="inherit" onClick={handleClickUser}>
                <Avatar alt="avatar" src={user['profile']['image_48']} className={classes.userAvatar} />
                {user['profile']['real_name']}
            </Button>
            <Popper open={Boolean(anchorEl)} anchorEl={anchorEl}>
                <Paper>
                    <ClickAwayListener onClickAway={handleClickAway}>
                        <MenuList autoFocusItem={Boolean(anchorEl)} onKeyDown={handleKeyDownUserMenu}>
                            <MenuItem onClick={handleSelectLogout}>ログアウト</MenuItem>
                        </MenuList>
                    </ClickAwayListener>
                </Paper>
            </Popper>
        </div>
    );
}