import React, {useState, useEffect, useCallback} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import userStore from '../stores/UserStore';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Container from '@material-ui/core/Container';
import TextField from '@material-ui/core/TextField';
import SearchIcon from '@material-ui/icons/Search';
import axios from 'axios';
import EditIcon from '@material-ui/icons/Edit';
import DoneIcon from '@material-ui/icons/Done';
import NativeSelect from '@material-ui/core/NativeSelect';
import Alert from '@material-ui/lab/Alert';
import Snackbar from '@material-ui/core/Snackbar';
import CancelIcon from '@material-ui/icons/Cancel';
import DeleteIcon from '@material-ui/icons/Delete';
import CssBaseline from '@material-ui/core/CssBaseline';
import Tooltip from '@material-ui/core/Tooltip';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Grid from '@material-ui/core/Grid';
import GetAppIcon from '@material-ui/icons/GetApp';
import { saveAs } from 'file-saver';
import UserAvatar from '../components/UserAvatar';
import SideMenu from '../components/SideMenu';
import DetailsIcon from '@material-ui/icons/Details';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Button from "@material-ui/core/Button";
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import Divider from '@material-ui/core/Divider';
import AccessTimeIcon from "@material-ui/icons/AccessTime";


const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1
    },
    title: {
        flexGrow: 1
    },
    table: {
        '& .MuiTableCell-root': {
            whiteSpace: 'nowrap'
        },
        '& .MuiTableHead-root': {
            '& .MuiTableRow-root': {
                '& .MuiTableCell-root': {
                    fontWeight: 600,
                    backgroundColor: theme.palette.success.main
                }
            }
        }
    },
    container: {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(5),
    },
    gridContainer: {
        marginBottom: theme.spacing(3)
    },
    tableToolbar: {
        '& .MuiFormControl-root': {
            marginRight: theme.spacing(2),
            marginLeft: theme.spacing(1),
            width: 100
        },
        marginBottom: theme.spacing(1)
    },
    holidayCell: {
        color: '#f44336',
    },
    customerField: {
        width: 50
    },
    dateField: {
        width:50
    }
}));

const dayOfWeek = ['月', '火', '水', '木', '金', '土', '日'];
const kindMap = {
    '0': '',
    '10': '有休', '11': '有休(AM)', '12': '有休(PM)',
    '20': '欠勤', '21': '欠勤(AM)', '22': '欠勤(PM)',
    '30': '特休', '31': '特休(AM)', '32': '特休(PM)',
    '40': '代休', '41': '代休(AM)', '42': '代休(PM)',
    '50': '休出'};

export default function TimeRecord() {
    const classes = useStyles();
    const [year, setYear] = useState(new Date().getFullYear());
    const [month, setMonth] = useState(new Date().getMonth() + 1);
    const [rows, setRows] = useState([]);
    const [severity, setSeverity] = useState('error');
    const [message, setMessage] = useState('');
    const [snackbarOpen, setSnackbarOpen] = useState(false);
    const [oldRows, setOldRows] = useState({});
    const [detailDialogOpen, setDetailDialogOpen] = useState(false);
    const [detailRow, setDetailRow] = useState({});
    const [summaryRows, setSummaryRows] = useState([]);

    /**
     * 勤怠記録リストを取得します。
     */
    const loadTimeRecords = useCallback(() => {
        axios.get(`${process.env.REACT_APP_REST_URI}/time_records/${year}/${month}`, {
            params: {
                token: userStore.getAccessToken()
            }
        }).then((response) => {
            const records = response.data['records'];
            const rows = [];
            records.forEach((record) => {
                rows.push({
                    edit: false,
                    id: record['time_record_id'],
                    year: record['year'],
                    month: record['month'],
                    date: record['date'],
                    day: dayOfWeek[record['day']],
                    holiday: record['holiday'] ? 'H' : '',
                    customer: record['customer'],
                    kind: record['kind'],
                    start_time: record['start_time'],
                    end_time: record['end_time'],
                    total_time: record['total_time'],
                    over_time: record['over_time'],
                    midnight_time: record['midnight_time'],
                    deduction_time: record['deduction_time'],
                    note: record['note']});
            });
            setRows(rows);
            setOldRows({});
        }).catch((error) => {
            setSeverity('error');
            setMessage(error['message']);
            setSnackbarOpen(true);
        });
    }, [year, month]);

    /**
     * 勤怠記録要約を取得します。
     */
    const loadSummary = useCallback(() => {
        axios.get(`${process.env.REACT_APP_REST_URI}/time_records/${year}/${month}/summary`, {
            params: {
                token: userStore.getAccessToken()
            }
        }).then((response) => {
            const records = response.data['records'];
            const rows = [];
            records.forEach((record) => {
                rows.push({
                    customer: record['customer'],
                    fixed_time: record['fixed_time'],
                    actual_time: record['actual_time'],
                    over_time: record['over_time'],
                    midnight_time: record['midnight_time'],
                    statutory_time: record['statutory_time'],
                    statutory_midnight_time: record['statutory_midnight_time'],
                    deduction_time: record['deduction_time']
                });
            });
            setSummaryRows(rows);
        }).catch((error) => {
            setSeverity('error');
            setMessage(error['message']);
            setSnackbarOpen(true);
        });
    }, [year, month]);

    useEffect(() => {
        loadTimeRecords();
        loadSummary();
    }, [year, month, loadTimeRecords, loadSummary]);

    /**
     * 検索ボタンがクリックされた場合に呼ばれます。
     */
    const handleClickSearch = () => {
        // 年の入力がない場合はエラー
        if (!year) {
            setSeverity('error');
            setMessage('年が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        // 年が2020年未満、または現在年を超える場合はエラー
        if (Number(year) < 2020 || Number(year) > new Date().getFullYear()) {
            setSeverity('error');
            setMessage('年は「2020≦入力年≦現在年」の範囲で入力してください。');
            setSnackbarOpen(true);
            return;
        }

        loadTimeRecords();
        loadSummary();
    };

    /**
     * ダウンロードボタンがクリックされた場合に呼ばれます。
     */
    const handleClickDownload = () => {
        // 年の入力がない場合はエラー
        if (!year) {
            setSeverity('error');
            setMessage('年が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        // 年が2020年未満、または現在年を超える場合はエラー
        if (Number(year) < 2020 || Number(year) > new Date().getFullYear()) {
            setSeverity('error');
            setMessage('年は「2020≦入力年≦現在年」の範囲で入力してください。');
            setSnackbarOpen(true);
            return;
        }

        axios.get(`${process.env.REACT_APP_REST_URI}/time_records/${year}/${month}/download`, {
            params: {
                token: userStore.getAccessToken()
            },
            responseType: 'blob'
        }).then((response) => {
            const blob = new Blob([response.data], {
                type: response.data.type
            });
            saveAs(blob, `勤怠${year}${month}.xlsx`);
        }).catch((error) => {
            setSeverity('error');
            setMessage(error['message']);
            setSnackbarOpen(true);
        });
    };

    /**
     * 確定ボタンがクリックされた場合に呼ばれます。
     *
     * @param row 勤怠情報
     */
    const handleClickDone = (row) => {
        const regex = RegExp(/\d{1,2}:\d{1,2}/);
        if (row.start_time && !regex.test(row.start_time)) {
            setSeverity('error');
            setMessage('始業は「HH:mm」形式で入力してください。');
            setSnackbarOpen(true);
            return;
        }
        if (row.end_time && !regex.test(row.end_time)) {
            setSeverity('error');
            setMessage('終業は「HH:mm」形式で入力してください。');
            setSnackbarOpen(true);
            return;
        }
        if (row.start_time && row.end_time && !validationTime(row.start_time, row.end_time)) {
            setSeverity('error');
            setMessage('始業は終業よりも前の時間を入力してください。');
            setSnackbarOpen(true);
            return;
        }

        const rowYear = row.year;
        const rowMonth = row.month;
        const rowDate = row.date;
        if (0 === row.id) {
            axios.post(`${process.env.REACT_APP_REST_URI}/time_records/${rowYear}/${rowMonth}/${rowDate}`, {
                customer: row.customer,
                kind: row.kind,
                start_time: row.start_time,
                end_time: row.end_time,
                note: row.note
            }, {
                params: {
                    token: userStore.getAccessToken()
                }
            }).then((response) => {
                recordToRow(response.data['record'], row);
                setRows(rows.slice());
                setSeverity('success');
                setMessage('更新しました。');
                setSnackbarOpen(true);
                loadSummary();
            }).catch((error) => {
                setSeverity('error');
                setMessage(error['message']);
                setSnackbarOpen(true);
            });

        }
        else {
            axios.put(`${process.env.REACT_APP_REST_URI}/time_records/${rowYear}/${rowMonth}/${rowDate}`, {
                customer: row.customer,
                kind: row.kind,
                start_time: row.start_time,
                end_time: row.end_time,
                note: row.note
            }, {
                params: {
                    token: userStore.getAccessToken()
                }
            }).then((response) => {
                recordToRow(response.data['record'], row);
                setRows(rows.slice());
                setSeverity('success');
                setMessage('更新しました。');
                setSnackbarOpen(true);
                loadSummary();
            }).catch((error) => {
                setSeverity('error');
                setMessage(error['message']);
                setSnackbarOpen(true);
            });
        }
    };

    /**
     * サービスから取得した勤怠記録情報を行データに設定します。
     *
     * @param record 勤怠記録情報
     * @param row 行データ
     */
    const recordToRow = (record, row) => {
        row.edit = false;
        row.id = record['time_record_id'];
        row.customer = record['customer'];
        row.kind = record['kind'];
        row.start_time = record['start_time'];
        row.end_time = record['end_time'];
        row.total_time = record['total_time'];
        row.note = record['note'];
    };

    /**
     * 編集ボタンがクリックされた場合に呼ばれます。
     *
     * @param row 勤怠情報
     */
    const handleClickEdit = (row) => {
        delete oldRows[String(row.date)];
        setOldRows(oldRows);
        row.edit = true;
        setRows(rows.slice());
    };

    /**
     * キャンセルボタンがクリックされた場合に呼ばれます。
     *
     * @param row 勤怠情報
     */
    const handleClickCancel = (row) => {
        const oldRow = oldRows[String(row.date)];
        if (oldRow) {
            row.customer = oldRow.customer;
            row.kind = oldRow.kind;
            row.start_time = oldRow.start_time;
            row.end_time = oldRow.end_time;
            row.total_time = oldRow.total_time;
            row.note = oldRow.note;
            delete oldRows[String(row.date)];
            setOldRows(oldRows);
        }
        row.edit = false;
        setRows(rows.slice());
    };

    /**
     * 削除ボタンがクリックされた場合に呼ばれます。
     *
     * @param row 勤怠情報
     */
    const handleClickDelete = (row) => {
        const rowYear = row.year;
        const rowMonth = row.month;
        const rowDate = row.date;
        axios.delete(`${process.env.REACT_APP_REST_URI}/time_records/${rowYear}/${rowMonth}/${rowDate}`, {
            params: {
                token: userStore.getAccessToken()
            }
        }).then(() => {
            row.id = 0;
            row.customer = null;
            row.kind = 0;
            row.start_time = null;
            row.end_time = null;
            row.total_time = null;
            row.note = null;
            setRows(rows.slice());
            setSeverity('success');
            setMessage('削除しました。');
            setSnackbarOpen(true);
            loadSummary();
        }).catch((error) => {
            setSeverity('error');
            setMessage(error['message']);
            setSnackbarOpen(true);
        });
    };

    /**
     * 勤怠情報をバックアップします。
     *
     * @param row 勤怠情報
     */
    const backupRow = (row) => {
        if (!oldRows.hasOwnProperty(String(row.date))) {
            oldRows[String(row.date)] = Object.assign({}, row);
        }
    };

    /**
     * 客先が変更された場合に呼ばれます。
     *
     * @param row 勤怠情報
     * @param customer 客先
     */
    const handleChangeCustomer = (row, customer) => {
        backupRow(row);
        row.customer = customer;
        setRows(rows.slice());
    };

    /**
     * 勤休が変更された場合に呼ばれます。
     *
     * @param row 勤怠情報
     * @param kind 勤休
     */
    const handleChangeKind = (row, kind) => {
        backupRow(row);
        row.kind = kind;
        setRows(rows.slice());
    };

    /**
     * 始業が変更された場合に呼ばれます。
     *
     * @param row 勤怠情報
     * @param start_time 始業
     */
    const handleChangeStartTime = (row, start_time) => {
        backupRow(row);
        row.start_time = start_time;
        setRows(rows.slice());
    };

    /**
     * 終業が変更された場合に呼ばれます。
     *
     * @param row 勤怠情報
     * @param end_time 終業
     */
    const handleChangeEndTime = (row, end_time) => {
        backupRow(row);
        row.end_time = end_time;
        setRows(rows.slice());
    };

    /**
     * 備考が変更された場合に呼ばれます。
     *
     * @param row 勤怠情報
     * @param note 備考
     */
    const handleChangeNote = (row, note) => {
        backupRow(row);
        row.note = note;
        setRows(rows.slice());
    };

    /**
     * 勤休の選択肢を描画するコンポーネントです。
     *
     * @returns {[]} 選択肢リスト
     * @constructor
     */
    const KindOptions = () => {
        const options = [];
        for (let [key, value] of Object.entries(kindMap)) {
            options.push(<option key={key} value={key}>{value}</option>);
        }
        return options;
    };

    /**
     * 月の選択肢を描画するコンポーネントです。
     *
     * @returns {[]} 選択肢リスト
     * @constructor
     */
    const MonthOptions = () => {
        const options = [];
        for (let month = 1; month <= 12; month++) {
            options.push(<option key={month} value={month}>{month}</option>);
        }
        return options;
    };

    /**
     * スナックバーの閉じるイベント時に呼ばれます。
     *
     * @param event イベント
     * @param reason 理由
     */
    const handleCloseSnackbar = (event, reason) => {
        if ('clickaway' === reason) {
            return;
        }
        setSnackbarOpen(false);
    };

    /**
     * 時間の前後検証を実施します。
     *
     * @param start 始業
     * @param end 終業
     * @returns {boolean} true: 検証OK, false: 検証NG
     */
    const validationTime = (start, end) => {
        const now = new Date();
        const starts = start.split(':');
        const ends = end.split(':');
        const sdt = new Date(now.getFullYear(), now.getMonth(), now.getDate(), Number(starts[0]), Number(starts[1]));
        const edt = new Date(now.getFullYear(), now.getMonth(), now.getDate(), Number(ends[0]), Number(ends[1]));
        return sdt < edt;
    };

    /**
     * 詳細ボタンがクリックされた場合に呼ばれます。
     */
    const handleClickDetails = (row) => {
        setDetailDialogOpen(true);
        setDetailRow(row);
    };

    /**
     * 時間情報アイテムコンポーネント
     *
     * @param props プロパティ
     * @returns {*}
     * @constructor
     */
    const TimeItem = (props) => {
        return (
            <ListItem>
                <ListItemAvatar>
                    <Avatar>
                        <AccessTimeIcon />
                    </Avatar>
                </ListItemAvatar>
                <ListItemText primary={props.primary} secondary={props.secondary} />
            </ListItem>
        );
    };

    return (
        <form className={classes.root}>
            <CssBaseline />
            
            <AppBar position="sticky">
                <Toolbar>
                    <SideMenu/>
                    <Typography variant="h6" className={classes.title}>
                        株式会社ベイクラフト
                    </Typography>
                    <UserAvatar/>
                </Toolbar>
            </AppBar>

            <Snackbar open={snackbarOpen} autoHideDuration={6000} onClose={handleCloseSnackbar}>
                <Alert severity={severity} variant="filled" onClose={handleCloseSnackbar}>{message}</Alert>
            </Snackbar>

            <Dialog open={detailDialogOpen} onClose={() => setDetailDialogOpen(false)} fullWidth={true} maxWidth="xs">
                <DialogTitle>{detailRow.year}/{detailRow.month}/{detailRow.date}</DialogTitle>
                <DialogContent>
                    <List>
                        <TimeItem primary="合計" secondary={detailRow.total_time} />
                        <Divider variant="inset" component="li" />
                        <TimeItem primary="残業" secondary={detailRow.over_time} />
                        <Divider variant="inset" component="li" />
                        <TimeItem primary="深夜残業" secondary={detailRow.midnight_time} />
                        <Divider variant="inset" component="li" />
                        <TimeItem primary="控除" secondary={detailRow.deduction_time} />
                    </List>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setDetailDialogOpen(false)} color="primary">
                        閉じる
                    </Button>
                </DialogActions>
            </Dialog>

            <Container maxWidth="lg" className={classes.container}>
                <Toolbar className={classes.tableToolbar}>
                    <Typography variant="h6" className={classes.title}>
                        勤怠記録
                    </Typography>
                    <TextField required={true} label="年" margin="normal" defaultValue={year} onChange={(event) => setYear(event.target.value)} />
                    <FormControl required={true} margin="normal">
                        <InputLabel htmlFor="select-month">月</InputLabel>
                        <NativeSelect value={month} inputProps={{id: 'select-month'}}
                                      onChange={(event) => setMonth(event.target.value)}>
                            <MonthOptions />
                        </NativeSelect>
                    </FormControl>
                    <Tooltip title="検索">
                        <IconButton onClick={handleClickSearch}>
                            <SearchIcon />
                        </IconButton>
                    </Tooltip>
                    <Tooltip title="ダウンロード">
                        <IconButton onClick={handleClickDownload}>
                            <GetAppIcon />
                        </IconButton>
                    </Tooltip>
                </Toolbar>
                <Grid container={true} spacing={1} className={classes.gridContainer}>
                    <Grid item={true} xs={8}>
                        <TableContainer>
                            <Table size="small" className={classes.table}>
                                <TableHead>
                                    <TableRow>
                                        <TableCell align="center">客先</TableCell>
                                        <TableCell align="center">所定時間</TableCell>
                                        <TableCell align="center">実働時間</TableCell>
                                        <TableCell align="center">普通残業</TableCell>
                                        <TableCell align="center">深夜残業</TableCell>
                                        <TableCell align="center">法休普残</TableCell>
                                        <TableCell align="center">法休深残</TableCell>
                                        <TableCell align="center">控除</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {summaryRows.map(row => (
                                        <TableRow key={row.customer}>
                                            <TableCell align="center">{row.customer}</TableCell>
                                            <TableCell align="center">{row.fixed_time}</TableCell>
                                            <TableCell align="center">{row.actual_time}</TableCell>
                                            <TableCell align="center">{row.over_time}</TableCell>
                                            <TableCell align="center">{row.midnight_time}</TableCell>
                                            <TableCell align="center">{row.statutory_time}</TableCell>
                                            <TableCell align="center">{row.statutory_midnight_time}</TableCell>
                                            <TableCell align="center">{row.deduction_time}</TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Grid>
                </Grid>
                <TableContainer>
                    <Table className={classes.table} aria-label="timerecords" size="small">
                        <TableHead>
                            <TableRow>
                                <TableCell align="center" scope="row">日付</TableCell>
                                <TableCell align="center">曜日</TableCell>
                                <TableCell align="center">休日</TableCell>
                                <TableCell align="center">客先</TableCell>
                                <TableCell align="center">勤休</TableCell>
                                <TableCell align="center">始業</TableCell>
                                <TableCell align="center">終業</TableCell>
                                <TableCell align="center">備考</TableCell>
                                <TableCell align="center">操作</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {rows.map(row => (
                                <TableRow key={row.date}>
                                    <TableCell align="center">{row.date}</TableCell>
                                    <TableCell align="center">{row.day}</TableCell>
                                    <TableCell align="center" className={classes.holidayCell}>{row.holiday}</TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <TextField defaultValue={row.customer} className={classes.customerField}
                                                       inputProps={{style: {textAlign: 'center'}}}
                                                       autoFocus={true}
                                                       onChange={(event) => handleChangeCustomer(row, event.target.value)} />
                                        ) : (
                                            <span>{row.customer}</span>
                                        )}
                                    </TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <NativeSelect value={row.kind}
                                                          onChange={(event) => handleChangeKind(row, event.target.value)}>
                                                <KindOptions />
                                            </NativeSelect>
                                        ) : (
                                            <span>{kindMap[row.kind]}</span>
                                        )}
                                    </TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <TextField defaultValue={row.start_time} className={classes.dateField}
                                                       inputProps={{style: {textAlign: 'center'}}}
                                                       onChange={(event) => handleChangeStartTime(row, event.target.value)} />
                                        ) : (
                                            <span>{row.start_time}</span>
                                        )}
                                    </TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <TextField defaultValue={row.end_time} className={classes.dateField}
                                                       inputProps={{style: {textAlign: 'center'}}}
                                                       onChange={(event) => handleChangeEndTime(row, event.target.value)} />
                                        ) : (
                                            <span>{row.end_time}</span>
                                        )}
                                    </TableCell>
                                    {row.edit ? (
                                        <TableCell align="center">
                                            <TextField defaultValue={row.note}
                                                       onChange={(event) => handleChangeNote(row, event.target.value)} />
                                        </TableCell>
                                    ) : (
                                        <TableCell><span>{row.note}</span></TableCell>
                                    )}
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <div>
                                                <IconButton aria-label="done" onClick={() => handleClickDone(row)}>
                                                    <DoneIcon />
                                                </IconButton>
                                                <IconButton aria-label="cancel" onClick={() => handleClickCancel(row)}>
                                                    <CancelIcon />
                                                </IconButton>
                                            </div>
                                        ) : (
                                            <div>
                                                <IconButton aria-label="edit" onClick={() => handleClickEdit(row)}>
                                                    <EditIcon />
                                                </IconButton>
                                                <IconButton aria-label="delete" onClick={() => handleClickDelete(row)}>
                                                    <DeleteIcon />
                                                </IconButton>
                                                <Tooltip title="詳細">
                                                    <IconButton aria-label="details" onClick={() => handleClickDetails(row)}>
                                                        <DetailsIcon />
                                                    </IconButton>
                                                </Tooltip>
                                            </div>
                                        )}
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </Container>
        </form>
    );
}
